import { Box, Grid, GridItem, Heading,  Text,  Image, Link } from "@chakra-ui/react";

import mainbg from '../StayProductive/mainbg.svg';
import Testimony from "../Testimony/Testimony";
import arrow from '../StayProductive/arrow.svg';

const StayProductive = () => {
  return ( 
    <Box>
      <Grid templateColumns='repeat(2, 1fr)' gap={0}  my={{base:'5px', md:'10px'}}>
      <GridItem bg='#F9F8FE' colSpan={{base:2, md:1}} p={10}>
        <Heading as='h2' fontFamily='heading' mb={4} mt={5} fontSize={{base:'16px', md:'28px',xl:'35px' }}>
          Stay Productive, Whereever You Are
        </Heading>
        <Text my={3} fontSize={{base:'12px', md:'16px',"lg":'17px' }} fontFamily='sans-serif' >
          Never let location be an issue when accessing your files. Fylo has you covered for all your storage needs.
          <br/>
          <br/>
          Secure share files and folders with friends, and family and colleagues for live collaboration. No email attachments required!
        </Text>
         <Link color='green'  fontSize={15} p={2}>
          See how Fylo works 
         <Image 
         display='inline'
         mt={2}
         src={arrow}
         />
         </Link>
         <Testimony />
      </GridItem>
        <GridItem bg='#F9F8FE' colSpan={{base:2, md:1}}>
         <Image  objectFit='cover' boxSize='90%'
            src={mainbg}
          />
        </GridItem>
      </Grid>
    </Box>
   );
}
 
export default StayProductive;
