import { Box, Grid, GridItem, Heading,  Input, Text, Button, Image } from "@chakra-ui/react";
 
import bg from '../SecuredFile/image.svg'

const SecuredFile = () => {
  return ( 
    <Box>
      <Grid templateColumns='repeat(2, 1fr)' gap={2} p={10} >
      <GridItem bg='#F0F0F0F' colSpan={{ base:2, md:1}} p={2}>
        <Heading as='h1' fontFamily='heading'fontSize={{base:'16px', md:'28px',lg:'35px' }}   my={4} textAlign='center' >
          All your files In One Secure  Location, Accessible Anywhere.
        </Heading>
        <Text fontSize={{base:'14px', md:'16px',xl:'17px' }} fontFamily='sans-serif' >Fylo stores your most important files in one secure location. Access <br/> them whereever you need, share and collaborate with friends, family, <br/> and co-workers.
        </Text>
         <Box>
          <Grid templateColumns='repeat(2, 1fr)' gap={1} mt={8} >
            <GridItem mt={{sm:'10px', md:0, lg:0}} colSpan={{base:2, md:1, lg:1}} >
            <Text textColor='red' fontSize='10px'>
            <Input placeholder="techaton#mail.com"  size={{base:'xs', md:'md'}} /> <br/>
            Please check your email
            </Text>
            </GridItem>
            <GridItem  colSpan={{base:2, md:1, lg:1}}>
              <Button size={{base:'sm', md:'md', }} w="100%" colorScheme='blue' >
                  Get Started
              </Button>
            </GridItem>
          </Grid>
         </Box>
      </GridItem>
        <GridItem bg='#F0F0F0F' colSpan={{base: 2, md:1}}>
         <Image  objectFit='cover' boxSize='90%'
            src={bg}
          />
        </GridItem>
      </Grid>
    </Box>
   );
}
 
export default SecuredFile;
