import { Grid, GridItem, Heading, Text, Stack, Input, Button } from "@chakra-ui/react";


const SemiFooter = () => {
  return ( 
    <Grid templateColumns='repeat(2, 1fr)' gap={0}>
      <GridItem bg='#575989' colSpan={{base:2, md:1}} p={10}>
      <Heading as='h2' textAlign={'center'} color='white' fontFamily='heading' mb={4} mt={5} fontSize={{base:'20px', md:'28px', lg:'35px' }}>Get Early Access Today
        </Heading>
        <Text color='white' mr={7} fontSize={{ base:'12px', md:'17px',lg:'18px' }} fontFamily='serif' >It only takes a minute to sign up and our free starter tier is extremely generous. If you have any questions, our support team would be happy to help you.
        </Text>
      </GridItem>
      <GridItem bg='#575989' colSpan={{base:2, md:1}} p={{base:'2px', md:'4px', lg:'8px'}} >
      <Stack p={{base:'5px', md:'10px'}} gap={2} mt={4} ml={{base:'1px', md:'7px'}}>
          <Text color='white' textColor='red' fontSize='10px'> 
          <Input placeholder="johnappleseed#mail.com" size={"sm"}  bg='white' w={{base:'100%', md:'80%'}} /><br/>
            Please check your email</Text>
          <Button mb={{base:'10px'}} size={'sm'} w={{base:'100%', md:'80%'}} colorScheme='blue' >Get started</Button>
         </Stack>
      </GridItem>
    </Grid>
   );
}
 
export default SemiFooter;