import { Grid, GridItem, Stack, Image, Text, Link, Box } from "@chakra-ui/react";
import logo from '../Footer/logo.svg';
import email from '../Footer/email.svg';
import phone from '../Footer/phone.svg';
import facebook from '../Footer/facebook.svg';
import twitter from '../Footer/twitter.svg';
import instagram from '../Footer/instagram.svg';


const Footer = () => {
  return ( 
    <Box>
      <Grid templateColumns='repeat(4, 1fr)' gap={0}>
      <GridItem colSpan={{ base:4, md:1, xl:1}} bg='#07043B' p={7}>
        <Stack my={7}>
          <Image 
          objectFit='cover'
          boxSize={{base:'70%', md:'80%'}}
          src={logo}
          p={2}
          my={3}
          />
          <Text color='white' fontSize={{base:'14px', md:'14px', lg:'16px'}}>
          <Image objectFit='contain' boxSize={3} src={phone} display='inline' mx={3}/>
           phone: +1-5345-3536
          </Text>
          <Text color='white' fontSize={{base:'14px', md:'114px', lg:'16px'}}>
          <Image objectFit='contain' boxSize={3} src={email} display='inline' mx={3}/>
           example@fylo.com
          </Text>
        </Stack>
      </GridItem>
      <GridItem colSpan={{base:4, md:1, xl:1}} bg='#07043B' p={7}>
        <Stack pb={{md:'10px'}} direction='column' gap={1} textColor='white' ml={{sm:'7px', md:'20px'}} mt={{base:'1px', md:'130px'}}>
          <Link>About us</Link>
          <Link>Pobs</Link>
          <Link>Press</Link>
          <Link>Blog</Link>
        </Stack>
      </GridItem>
      <GridItem colSpan={{base:4, md:1, xl:1}} bg='#07043B' p={7}>
        <Stack pb={{md:'10px'}} direction='column' gap={1} textColor='white' ml={{sm:'7px', md:'20px'}} mt={{sm:'1px', md:'130px'}}>
          <Link>Contact Us</Link>
          <Link>Terms</Link>
          <Link>Privacy</Link>
        </Stack>
      </GridItem>
      <GridItem colSpan={{base:4, md:1, xl:1}} bg='#07043B' p={7}>
        <Stack pb={{md:'10px'}} direction='row' gap={1} textColor='white' ml={{sm:'7px', md:'20px'}} mt={{base:'1px', md:'130px'}} >
        <Link>
        <Image objectFit='contain' boxSize={6} src={facebook} display='inline' mx={2}/>
        </Link>
        <Link>
        <Image objectFit='contain' boxSize={6} src={twitter} display='inline' mx={2} />
        </Link>
        <Link>
        <Image objectFit='contain' boxSize={6} src={instagram} display='inline' mx={2}/>
        </Link>
        </Stack>
      </GridItem>
    </Grid>
    <Box bg='#07043B' p={6}><Text color="white" align='center'>@Techaton-1  2022</Text></Box>
    </Box>
   );
}
 
export default Footer;